import { NgModule ,NO_ERRORS_SCHEMA} from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { MaterialIssueRoutingModule } from './material-issue-routing.module';
import { MaterialIssueComponent } from './material-issue.component';
import { MaterialIssueAddComponent } from './material-issue-add/material-issue-add.component';
import { MaterialIssueEditComponent } from './material-issue-edit/material-issue-edit.component';
import { MaterialIssueViewComponent } from './material-issue-view/material-issue-view.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    MaterialIssueRoutingModule
  ],
  entryComponents:[
    MaterialIssueAddComponent,
    MaterialIssueEditComponent,
    MaterialIssueViewComponent
  ],
  declarations: [
    MaterialIssueComponent,
    MaterialIssueAddComponent,
    MaterialIssueEditComponent,
    MaterialIssueViewComponent
  ],
  schemas: [ NO_ERRORS_SCHEMA ]
})
export class MaterialIssueModule { }



