import { Component, OnInit, Input, Inject, AfterViewInit, ChangeDetectorRef, Output, EventEmitter, ViewChild } from '@angular/core';
import { MaterialIssueService } from '../../../../shared';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';

@Component({
  selector: 'app-material-issue-view',
  templateUrl: './material-issue-view.component.html',
  styleUrls: ['./material-issue-view.component.scss']
})
export class MaterialIssueViewComponent implements OnInit {
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @Input('MaterialIssueDetailViewData') MaterialIssueDetailViewData;

  source: any;
  dataAdapter: any;
  columns: any[]; 
  apiUrl: string;
  material_item: Array<any> = [];

  constructor(private cdr: ChangeDetectorRef,private mis: MaterialIssueService) { }

  ngOnInit() {
    this.getItem();
  }

  ngAfterViewInit() {
    this.jqxLoader.open();
    this.mis.show(this.MaterialIssueDetailViewData.id).subscribe(
      result => { 
        this.material_item = result['data'].material_issue_details;
        for (var _i = 0; _i < this.material_item.length; _i++) {
            this.material_item[_i]['name'] = this.material_item[_i]['item'] && this.material_item[_i]['item'].name;
        }
        this.source.localdata = this.material_item;
        this.myGrid.updatebounddata("cells");
        this.jqxLoader.close();
      }, 
      error => { 
        console.log(error);
      }
    );
    this.cdr.detectChanges();
  }

  getItem(){
    this.source =
    {
        datatype: 'json',
        datafields:

        [
          { name: 'name', type: 'string' },
          { name: 'item_id', type: 'string' },
          { name: 'quantity', type: 'string' },
          { name: 'rate', type: 'string' },
          { name: 'value', type: 'string' },
          { name: 'consumed_detail', type: 'string' },
          { name: 'dr_account_head_name', type: 'string',map:'dr_account_head>name' },
          { name: 'dr_account_head_id', type: 'string' },
        ],
    };
    this.dataAdapter = new jqx.dataAdapter(this.source);

    this.columns =
    [   
      {
        text: 'Item Name', datafield: 'item_id', displayfield: 'name', columntype: 'combobox', width: 120,
        createeditor: (row: number, column: any, editor: any): void => {
          let contractorsource =
            {
              datatype: "json",
              contentType: "application/json; charset=utf-8",
              datafields: [
                { name: 'id' },
                { name: 'name' },
              ],
              async: false,
              url: this.apiUrl + 'item?limit=all'
            };
          let contractorAdapter = new jqx.dataAdapter(contractorsource, {
            downloadComplete: (data, status, xhr) => {
              let tData = data.data;
              return tData;
            }
          });
          editor.jqxComboBox({
            source: contractorAdapter,
            displayMember: "name",
            valueMember: "id"
          });
        }
      },
      { text: 'Quantity', datafield: 'quantity', columntype: 'textbox' },
      { text: 'Rate', datafield: 'rate', columntype: 'textbox' },
      { text: 'Value', datafield: 'value', columntype: 'textbox' },
      { text: 'Consumed Detail', datafield: 'consumed_detail', columntype: 'textbox' },
      { text: 'Dr Account Head name', datafield: 'dr_account_head_id', displayfield: 'dr_account_head_name',columntype: 'textbox' },
    ];
 
  }
  rendertoolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';
    let headingContainer4 = document.createElement('div');
  
    headingContainer4.id = 'headingContainer4';
  
    headingContainer4.style.cssText = 'float: left;padding:5px;margin-right:10px;font-weight:bold;';
  
    headingContainer4.innerHTML = 'Material Issue Item Details:';
  
    container.appendChild(headingContainer4);
    toolbar[0].appendChild(container);
  
  };

}



